nav {
  background-color: rgba(16,16,24,.89);
  width: 100%;
  display: block;
  z-index: 2;
  position: fixed;
  height: 80px;
  /* left: 50%; */
  /* transform: translateX(-50%); */
  /* bottom: 2rem; */
  display: flex;
  
  border-radius: 0%;
  backdrop-filter: blur(15px);
}
nav > a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
  margin: auto;
  /* margin-left: 200px; */

  
}
nav:hover {
  background: rgba(0, 0, 0, 0.3);
}

